body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

@media (max-width: 849px){
   body {
      overflow-y: auto;
   }
}
.ArrowContainer {
   position: fixed;
   text-align: center;
}

.left {
   left: 0;
}

.right {
   right: 0;
}

.left, .right {
   top: 40%;
   width: -webkit-fit-content;
   width: -moz-fit-content;
   width: fit-content;
}

.up {
   top: 0;
}

.down {
   bottom: 0;
}

.up, .down {
   left: 0;
   width: 100vw;
   height: -webkit-fit-content;
   height: -moz-fit-content;
   height: fit-content;
}

.ArrowLink {
   font-size: 6em;
   font-weight: bold;
   opacity: 0.5;
   text-decoration: none;
   transition: 0.3s;
}

.ArrowLink:hover {
   opacity: 1;
   -webkit-transform: scale(1.1);
           transform: scale(1.1);
}

.DisabledArrow {
   display: none;
}

@media (max-width: 479px){
   .ArrowLink {
      font-size: 6em;
   }
}
.Loader {
   display: inline-block;
   margin: 10vh 46.5vw 0;
   width: 7vw;
   height: 7vw;
   border-top: 0.5vh solid black;
   border-bottom: 0.5vh solid black;
   border-radius: 50%;
   -webkit-animation: spin 2s infinite;
           animation: spin 2s infinite;
}

@-webkit-keyframes spin {
   50% {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
   }

   100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
   }
}

@keyframes spin {
   50% {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
   }

   100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
   }
}

@media (max-width: 849px){
   .Loader {
      width: 20vw;
      height: 20vw;
      margin: 10vh 35vw 0;
   }
}
.AboutPage h1 {
   font-size: 2.5em;
}

.AboutPage h2 {
   -webkit-animation-delay: 0.5s;
           animation-delay: 0.5s;
}

.JokeBlock {
   text-align: center;
   padding-top: 12vh;
   font-size: 1.1em;
   -webkit-animation-delay: 1s;
           animation-delay: 1s;
}

@media (max-width: 479px){
   .AboutPage h1 {
      font-size: 2.8em;
      margin-top: 0;
   }

   .AboutPage h2 {
      margin-top: 8vh;
      font-size: 1.6em;
   }

   .JokeBlock {
      padding-top: 4vh;
      font-size: 1.2em;
   }
}
.ContentsTable {
   width: 30vw;
}

.ContentsTable ul {
   width: -webkit-fit-content;
   width: -moz-fit-content;
   width: fit-content;
   margin: 0 auto;
   padding: 0;
}

.ContentsTable:nth-child(1) {
   -webkit-animation-delay: 0.3s;
           animation-delay: 0.3s;
}

.ContentsTable:nth-child(2) {
   -webkit-animation-delay: 0.7s;
           animation-delay: 0.7s;
}

.ContentsTable:nth-child(3) {
   -webkit-animation-delay: 1.1s;
           animation-delay: 1.1s;
}

@media (max-width: 849px){
   .ContentsTable {
      width: 80vw;
      margin-bottom: 2vh;
      font-size: 1.3em;
   }
}
.TableList {
   display: flex;
   justify-content: center;
   margin-top: 8vh;
}

.TechPage > h2 {
   font-size: 1.8em;
}

@media (max-width: 849px){
   .TechPage {
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
   }

   .TechPage > h2 {
      font-size: 2.3em;
   }

   .TableList {
      flex-direction: column;
      align-items: center;
      margin-top: 2vh;
   }
}
.IconLink {
   display: inline-block;
   margin-right: 0.8vw;
}

.IconLink:hover img {
   -webkit-transform: scale(1.07);
           transform: scale(1.07);
}

.IconCircle {
   border: 1px solid gray;
   border-radius: 50%;
   padding: 2vw;
   box-shadow: 2px 2px rgb(102, 95, 95);
   -webkit-animation-delay: 0.8s;
           animation-delay: 0.8s;
}

.IconCircle:hover {
   border-color: black;
}

.Icon {
   width: 7vh;
   height: 7vh;
}

.BigIcon {
   width: 7vw;
   height: 7vw;
}

@media (max-width: 849px){
   .Icon {
      display: none;
   }

   .BigIcon {
      width: 14vh;
      height: 14vh;
   }

   .IconCircle {
      padding: 6vh;
      margin-top: 5vh;
      text-align: center;
   }

   .IconCircle:last-of-type {
      margin-bottom: 0;
   }
}
.AppThumbnail {
   display: inline-block;
   text-align: left; 
   margin-bottom: 8vh;
}

.AppLink {
   display: block;
   text-decoration: none;
   width: 16vw; 
   height: 16vw;
   padding-top: 4vh;
   margin-bottom: 2vh;
   box-shadow: 2px 2px rgb(102, 100, 100);
}

.AppLink h3 {
   margin-top: 0;
   background: inherit;
   -webkit-background-clip: text;
           background-clip: text;
   color: transparent;
   -webkit-filter: invert(1);
           filter: invert(1);
}

.AppLink:hover .AppLogo, .AppLink:hover h3 {
   -webkit-transform: scale(1.05);
           transform: scale(1.05);
}

.AppLogo {
   width: 8vw;
   height: 8vw;
   margin: 1vh 4vw;
}

@media (max-width: 849px){
   .AppLink {
      width: 70vw;
      height: 70vw;
      font-size: 1.3em;
      padding-top: 6vh;
   }

   .AppThumbnail {
      margin-bottom: 3vh;
   }

   .AppThumbnail:last-child {
      margin-bottom: 0;
   }

   .AppLogo {
      width: 28vw;
      height: 28vw;
      margin: 2vh 21vw 0;
   }
}
.AppsList {
   display: flex;
   flex-wrap: wrap;
   justify-content: space-around;
   margin: 8vh auto 0;
}

@media (max-width: 849px){
   .AppsList {
      flex-direction: column;
      flex-wrap: nowrap;
      width: 70vw;
      margin: 0 auto;
      padding: 0;
   }
}
.AppsPage h2 {
   margin-top: 0; 
   font-size: 2em;
}

@media (max-width: 479px){
   .AppsPage {
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      padding: 0;
   }

   .AppsPage h2 {
      font-size: 2.6em;
      margin: 5vh 0 7vh;
   }
}
.ContactPage {
   padding: 18vh 0;
}

.ContactPage h2 {
   font-size: 2em;
}

.BigIconList {
   margin: 13vh auto 0;
   width: 50%;
   display: flex;
   justify-content: space-around;
}

.DownloadButton {
   display: block;
   width: -webkit-fit-content;
   width: -moz-fit-content;
   width: fit-content;
   margin: 11vh auto 0;
   padding: 3vh 2vw;
   background-color: black;
   color: lightgray;
   text-decoration: none;
   font-weight: bold;
   border-radius: 4px;
   box-shadow: 2px 2px black;
   transition: all 0.15s ease-in 0s;
   transition: box-shadow 0.05s ease-in 0s;
   -webkit-animation-delay: 1.5s;
           animation-delay: 1.5s;
}

.DownloadButton:hover {
   background-color: rgb(212, 207, 207);
   color: black;
}

.DownloadButton:active {
   box-shadow: 0.5px 0.5px black;
}

@media (max-width: 849px){
   .ContactPage {
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      padding-bottom: 7vh;
   }

   .ContactPage h2 {
      font-size: 2.6em;
      margin-bottom: 1vh;
   }

   .BigIconList {
      flex-direction: column;
      margin-top: 0;
   }
}
.App {
   margin: 0;
   padding: 0;
   width: 100vw;
   height: 100vh;
   background: #7F7FD5;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
   background: linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.Container {
   padding: 18vh 0;
}

h1, h2, h3 {
   text-align: center;
}

@media (max-width: 549px){
   .App {
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      width: 90vw;
      padding: 0 5vw;
   }
}
